@use '../../style/mixin.scss' as mixin;
@use '../../style/colors.scss' as color;

.container {
  @include mixin.container;

  @include mixin.tablet {
    padding-top: 10px;
    margin-bottom: 100px;
  }

  @include mixin.laptop {
    padding-top: 20px;
    width: 70%;
    margin-bottom: 200px;
  }
}

.btnBox {
  padding: 10px;
  margin-top: 0;

  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form {
  padding: 10px;
  margin-top: 70px;

  @include mixin.laptop{
    margin-top: 50px;
  }
}